import { Container, Row, Col } from "react-bootstrap";
import { MailchimpForm } from "./MailchimpForm";
import logo from "../assets/img/logo.png";
import navIcon1 from "../assets/img/nav-icon1.svg";
import navIcon2 from "../assets/img/nav-icon2.svg";
import navIcon3 from "../assets/img/nav-icon3.svg";

export const Footer = () => {
  return (
    <footer className="footer">
      <Container>
        <Row className="align-items-center">
          
          <Col size={12} sm={6}>
            <img className="logo" src={logo} alt="Logo" />
          </Col>
          <Col size={12} sm={6} className="text-center text-sm-end">
            <div className="social-icon">
              <a href="https://www.linkedin.com/in/aymenguedri/"><img src={navIcon1} alt="Icon" /></a>
              <a href="https://www.facebook.com/Aymenguedri.me"><img src={navIcon2} alt="Icon" /></a>
              <a href="https://www.instagram.com/_aymenguedri/"><img src={navIcon3} alt="Icon" /></a>
            </div>
            <p> Made with love ❤️ and magic</p>
            <p> &copy;2024 Aymen Guedri</p>
          </Col>
        </Row>
      </Container>
    </footer>
  )
}
