import React, { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import headerImg from "../assets/img/aymen.png";
import { ArrowRightCircle } from 'react-bootstrap-icons';
import 'animate.css';
import TrackVisibility from 'react-on-screen';

export const Banner = () => {
  const [loopNum, setLoopNum] = useState(0);
  const [isDeleting, setIsDeleting] = useState(false);
  const [text, setText] = useState('');
  const toRotate = ["a Full stack Developer", "a Designer", " an Entrepreneur"];
  const typingDelay = 300; 
  const deletingDelay = 50;
  const pauseDelay = 1500; 

  useEffect(() => {
    let ticker = setTimeout(() => {
      tick();
    }, isDeleting ? deletingDelay : typingDelay);

    return () => { clearTimeout(ticker) };
  }, [text, isDeleting])

  const tick = () => {
    const i = loopNum % toRotate.length;
    const fullText = toRotate[i];
  
    if (isDeleting) {
      // Deleting action
      setText((prevText) => prevText.slice(0, -1));
    } else {
      // Typing action
      setText((prevText) => fullText.slice(0, prevText.length + 1));
    }
  
    // Determine when to switch between typing and deleting
    if (!isDeleting && text === fullText) {
      setTimeout(() => {
        setIsDeleting(true);
      }, pauseDelay);
    } else if (isDeleting && text === '') {
      setTimeout(() => {
        setIsDeleting(false);
        setLoopNum((prevLoopNum) => prevLoopNum + 1);
      }, pauseDelay);
    }
};



  return (
    <section className="banner" id="home">
      <Container>
        <Row className="aligh-items-center">
          <Col xs={12} md={6} xl={7}>
            <TrackVisibility>
              {({ isVisible }) => (
                <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                  <h1 className="intro">Hi, it's me</h1>
                  <h1 className="intro">Aymen Guedri</h1>
                  <h1>
                    {`and I'm`}{" "}
                    <span
                      className="txt-rotate"
                      dataPeriod="3000"
                      data-rotate='[ "Full stack Developer", "Designer", "Entrepreneur" ]'
                    >
                      <span className="wrap">{text}</span>
                    </span>
                  </h1>
                  <button onClick={() => console.log('connect')}>Let's Connect <ArrowRightCircle size={25} /></button>
                </div>
              )}
            </TrackVisibility>
          </Col>
          <Col xs={12} md={6} xl={5}>
            <TrackVisibility>
              {({ isVisible }) => (
                <div className={isVisible ? "animate__animated animate__zoomIn" : ""}>
                  <img src={headerImg} alt="Header Img"/>
                </div>
              )}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
    </section>
  );
};
